html {
  --segmented-button-background-hover: #e5e5e5;
  --segmented-button-background-pressed: #d6d6da;
  --segmented-button-background-active: var(--brand-secondary);
  --segmented-button-color: var(--brand-secondary);
  --segmented-button-color-error: var(--interface-error);
  --segmented-button-color-disabled: var(--gray-2);
  --segmented-button-color-active: var(--white);
  --segmented-button-border: var(--brand-secondary);
  --segmented-button-border-error: var(--interface-error);
  --segmented-button-border-disabled: var(--gray-2);
  --segmented-button-outline: var(--accent-2);
  --segmented-button-width: 152px;
  --segmented-button-height: 48px;
}
