/**
  * @tokens Spacings
  * @presenter Spacing
  */
html {
  --spacing-xxxxs: 0.25rem; // 4px
  --spacing-xxxs: 0.5rem; // 8px
  --spacing-xxs: 0.75rem; // 12px
  --spacing-xs: 1rem; // 16px
  --spacing-s: 1.5rem; // 24px
  --spacing-m: 2rem; // 32px
  --spacing-l: 2.5rem; // 40px
  --spacing-xl: 3rem; // 48px
  --spacing-xxl: 3.5rem; // 56px
  --spacing-xxxl: 4rem; // 64px

  // 768px
  @media (min-width: 48em) {
    --spacing-xxl: 4rem; // 64px
    --spacing-xxxl: 6rem; // 96px
  }

  // 1366px
  @media (min-width: 85.375em) {
    --spacing-xl: 3.5rem; /* 48px mobile & tablet, 56px desktop  */
    --spacing-xxl: 5rem; /* 56px mobile, 64px tablet, 80px desktop  */
    --spacing-xxxl: 7.5rem; /* 64px mobile, 96px tablet, 120px desktop  */
  }
}
