@import '../../assets/styles/h3Imports';
@import '../../assets/styles/themes/default/index';

.segmented-button {
  display: inline-block;

  &__left {
    border: 2px solid var(--segmented-button-border);
    border-radius: 30px 0 0 30px;
  }

  &__right {
    border: 2px solid var(--segmented-button-border);
    border-left: none;
    border-radius: 0 30px 30px 0;
  }

  &__left,
  &__right {
    @include button-text;

    background-color: transparent;
    color: var(--segmented-button-color);
    font-weight: 700;
    margin: 0;
    min-height: var(--segmented-button-height);
    min-width: var(--segmented-button-width);
    position: relative;
    transition: background-color 0.5s;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;

    &:hover {
      background-color: var(--segmented-button-background-hover);
    }

    &:active {
      background-color: var(--segmented-button-background-pressed);
    }

    &:focus-visible {
      outline: 2px solid var(--segmented-button-outline);
      z-index: 1;
    }

    &--error {
      border-color: var(--segmented-button-border-error);
      color: var(--segmented-button-color-error);
    }

    &--active {
      background-color: var(--segmented-button-background-active);
      border-color: var(--segmented-button-background-active);
      color: var(--segmented-button-color-active);

      &:hover,
      &:active {
        background-color: var(--segmented-button-background-active);
      }
    }

    &--disabled {
      border-color: var(--segmented-button-border-disabled);
      color: var(--segmented-button-color-disabled);
      pointer-events: none;

      &:focus-visible {
        outline: none;
      }
    }
  }
}

.segmented-button-caption {
  @include caption-text;

  color: var(--segmented-button-color-error);
}
