html {
  --dropdown-icon: var(--accent-interactive);
  --search-outline-focus: #0082aa;
  --input-outline-color: var(--input-focus);
  --input-hover-outline-color: var(--input-hover);
  --textarea-outline-color: var(--input-hover);
  --input-dropdown-focus: var(--interface-info);
  --input-box-shadow-transition: box-shadow 0.3s;
}
