html {
  --picker-weekday-color: var(--gray-3);
  --picker-day-outside-color: var(--gray-3);
  --picker-day-color: var(--accent-interactive);
  --picker-max-width: 381px;
  --picker-day-active-background: var(--accent-interactive);
  --picker-day-active-color: var(--white);
  --picker-day-disabled: var(--gray-2);
  --picker-day-hover: var(--text-button-hover-background);
  --picker-day-pressed-background: #d6d6da;
}
