html {
  // body
  --body-background-color: #f5f5f0; //#e5e5e5;

  /**
  * @tokens Default Primary colors
  * @presenter Color
  */

  // Primary colors
  --brand-primary: #e52600;
  --brand-secondary: #323264;
  --background-1: #f5f5f0;
  --background-2: #c2e8ff;

  /**
  * @tokens Default Accent colors
  * @presenter Color
  */
  // Accent colors
  --accent-1: #aa0a0a;
  --accent-2: #0082aa;
  --accent-3: #f9f9f6;
  --accent-4: #bc5724;
  --accent-interactive: #284b9b;
  --accent-selected: #274b9b;

  /**
  * @tokens Default Grayscale
  * @presenter Color
  */
  // Grayscale

  --gray-0: #f1f3f3;
  --gray-1: #d9dada;
  --gray-2: #a5aaaa;
  --gray-3: #606565;
  --gray-4: #414646;
  --gray-5: #0a1414;
  --gray-6: #404040;
  --white: #ffffff;
  --black: #000000;

  /**
  * @tokens Default Interface
  * @presenter Color
  */
  // Interface

  --interface-info: #284b9b;
  --interface-info-background: #f6f8fb;
  --interface-success: #00853e;
  --interface-success-background: #f5faf7;
  --interface-warning: #f26522;
  --interface-warning-background: #fffbf6;
  --interface-error: #e10900;
  --interface-error-background: #fef5f5;

  // --interface-light-grey: #f1f3f3;

  /**
  * @tokens Default Input Colors
  * @presenter Color
  */
  --input-default: var(--gray-3); /* #606565 */
  --input-hover: var(--gray-3); /* #414646 */
  --input-focus: var(--accent-interactive); /* #284B9B */
  --input-disabled: var(--gray-2); /* #A5AAAA */
  --input-error: var(--interface-error); /* #E10900 */
  --input-disabled-background: var(--gray-1); /* #D9DADA */
  --input-read-only-background: var(--gray-0); /* #D9DADA */
  --toggle-check-radio: var(--accent-interactive); /* #284B9B */
  --toggle-inactive: var(--gray-3); /* 606565 */
  --input-button-hover: rgba(50, 50, 100, 0.08);
  --input-button-focus: var(--accent-2); /* #0082aa */
  --input-dropdown-focus: var(--accent-2); /* #0082aa */
  --text-color: var(--gray-5);
  --input-text-read-only: var(--gray-4);
  --input-text-disabled: var(--gray-3);
  --link-color: var(--accent-interactive);
  --link-color-disabled: var(--gray-2, #a5aaaa);
  --input-icon-disabled: var(--gray-3);
  --input-icon-readonly: var(--gray-4);
  --scrollbar-background-color: var(--gray-2);
  --scrollbar-bordercolor: var(--white);

  /**
  * @tokens Default Footer Colors
  * @presenter Color
  */
  --footer-copyright-text: var(--gray-5, #0a1414);
  --footer-banner-background: var(--background-2, #c2e8ff);
  --footer-banner-texts: var(--gray-5, #0a1414);
  --footer-banner-top-border: var(--brand-secondary, #323264);
  --footer-border-top: rgba(50, 50, 100, 0.16);

  /**
  * @tokens Default Starting Banner Colors
  * @presenter Color
  */
  --starting-banner-background: var(--background-2, #c2e8ff);
  --starting-banner-texts: var(--gray-5, #0a1414);

  /**
  * @tokens Default SVG background stroke Color
  * @presenter Color
  */
  --svg-background-stroke: #e52600;

  /**
  * @tokens Default colors medication schedule
  * @presenter Color
  */

  // missed
  --missed-border: #e52600;
  --failed-red: #e10900;
}
