html {
  --text-button-color: var(--accent-interactive);
  --text-button-outline: #37a9cc;
  --text-button-hover-background: #e5e5e5;
  --text-button-pressed-background: #d6d6da;
  --text-button-delete-color: var(--interface-error);
  --text-button-delete-color-pressed: var(--interface-error);
  --dark-text-button-color: var(--white);
  --dark-text-button-background: var(--brand-secondary);
  --dark-text-button-hover-background: #2a2a57;
  --dark-text-button-pressed-background: #222248;
  --dark-disabled-text-button-background: #adadc1;
}
