html {
  /**
  * @tokens Shadows
  * @presenter Shadow
  */
  // shadow
  --box-shadow: 0 5px 14px rgba(10, 20, 20, 0.1);
  --box-shadow-notification: 0 5px 14px rgba(40, 75, 155, 0.09);
  --box-shadow-modal: 0 5px 20px 6px rgba(10, 20, 20, 0.16);
  --interface-info-shadow: 0 5px 14px rgba(40, 75, 155, 0.09);
  --interface-success-shadow: 0 5px 14px rgba(0, 133, 62, 0.09);
  --interface-warning-shadow: 0 5px 14px rgba(96, 101, 101, 0.09);
  --interface-error-shadow: 0 5px 14px rgba(225, 9, 0, 0.09);

  /**
  * @tokens Border Radius
  * @presenter BorderRadius
  */
  // border-radius
  --border-radius: 8px;
  --border-radius-medium: 24px;
  --border-radius-button: 50px;
  --border-radius-button-small: 44px;
}
