html {
  --tab-item-background: var(--background-1);
  --tab-item-color-unselected: var(--gray-3);
  --tab-item-color-hover: var(--accent-interactive);
  --tab-item-outline: var(--accent-2);
  --tab-bar-border: var(--gray-2);
  --tab-item-min-width: 114px;
  --tab-item-min-height: 48px;
  --button-tab-color: var(--brand-secondary);

  // TODO: Update background color for default theme (currently don't have in design)
  --button-tab-bar-background: #efedf1;
  --button-tab-bar-background-active: var(--brand-secondary);
  --button-tab-border-radius: 50px;
}
